import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import { ColEditDelete, TableData } from 'components/common/index';
import { getPrizeNotes } from 'services/prizeNote';
import { getQueryParams } from 'utils/urlUtil';
import FormFilterScholarship from './FormFilterScholarship';

class AddScholarshipPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  render() {
    const columns = [
      {
        title: 'M SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'birthdate',
        key: 'birthdate',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'S Q',
        dataIndex: 'regNum',
        key: 'regNum',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'decDay',
        key: 'decDay',
      },
      {
        title: 'Cp khen thng',
        dataIndex: 'rewardLevel',
        key: 'rewardLevel',
      },
      {
        title: 'Hnh thc',
        dataIndex: 'form',
        key: 'form',
      },
      {
        title: 'Loi khen thng',
        dataIndex: 'typeOfReward',
        key: 'typeOfReward',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/scholarship/updateForm/${record.code}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Nhp khen thng">
        <Row>
          <Col span={24}>
            <FormFilterScholarship />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/reward/addForm" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPrizeNotes}
              paramSearch={this.state.paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddScholarshipPage />
  </Page>
);



